import Vue from 'vue'
import Vuex from 'vuex'
import {
	request
} from '../common/js/request.js'


Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		token: '',
		userInfo: {}, //
		updateCart: true, // 
		cartCount: 0, //
		orderCount: {}, //

		collectCount: 0, //
		couponCount: 0, //
	},
	getters: {
		// 
		hasLogin(state) {
			return !!state.token;
		},
		getUpdateCart(state) {
			return state.updateCart
		},
		getCartCount(state) {
			return state.cartCount
		}
	},
	mutations: {
		//state
		setStateAttr(state, param) {
			if (param instanceof Array) {
				for (let item of param) {
					state[item.key] = item.val;
				}
			} else {
				state[param.key] = param.val;
			}
		},
		//token
		setToken(state, data) {
			const token = data;
			state.token = token;
			uni.setStorageSync('token', token);
			this.dispatch('getUserInfo'); //
			this.dispatch('getCartCount'); //
			this.dispatch('getCollectCount'); //
			this.dispatch('getOrderCount'); //
		},
		//
		logout(state) {
			state.token = '';
			uni.removeStorageSync('token');
			uni.removeStorageSync('historyCount'); //
			state.updateCart = true; //
			state.collectCount = 0; //
			state.orderCount = {}, //
				setTimeout(() => {
					state.userInfo = {};
				}, 1000)
		},
		// 
		setUpdateCart(state, flag) {
			console.log("flag" + Boolean(flag));
			state.updateCart = Boolean(flag);
		},
		//
		setCartCount(state, cartCount) {
			state.cartCount = cartCount;
		}
	},
	actions: {
		//
		async getUserInfo({
			state,
			commit
		}) {
			const token = state.token;
			const res = await request({
				url: `/user/detail?token=${token}`
			});
			if (res.code === 0) {
				const userInfo = res.data.base;
				commit('setStateAttr', {
					key: 'userInfo',
					val: userInfo
				})
			}
		},
		//
		async getCartCount({
			state,
			commit
		}) {
			let cartCount = 0;
			const res = await request({
				url: '/shopping-cart/info'
			});
			if (res.code === 0) {
				cartCount = res.data.number;
			}
			commit('setStateAttr', {
				key: 'cartCount',
				val: cartCount
			})
		},
		//
		async getOrderCount({
			state,
			commit
		}) {
			let orderCount = {};
			const res = await request({
				url: '/order/statistics'
			});
			if (res.code === 0) {
				orderCount = res.data;
			}
			commit('setStateAttr', {
				key: 'orderCount',
				val: orderCount
			})
		},
		// 
		async getCollectCount({
			state,
			commit
		}) {
			let collectCount = 0;
			const res = await request({
				method: 'post',
				url: '/shop/goods/fav/list',
				data: {
					type: 0
				}
			});
			if (res.code === 0) {
				collectCount = res.data.length;
			}
			commit('setStateAttr', {
				key: 'collectCount',
				val: collectCount
			})
		}
	}
})


export default store
