// 
// let ajaxTimes = 0;
import {
	msg,
	isLogin
} from './util.js'
import store from '@/store'


/**
 * $request
 * @param {Boolean} data 
 * @param {Boolean} ext 
 * @param {Boolean} ext.showLoading loading
 * @param {Boolean} ext.login 
 */
export const request = (params = {}, ext = {}) => {
	// ajaxTimes++;
	// uni.showLoading({
	// 	title: '...'
	// });
	if (ext.login && !isLogin()) {
		return {
			code: 10000
		};
	}
	params.data = {
		...params.data,
		token: store.state.token
	}
	const baseUrl = "https://api.it120.cc/zxapi";
	return new Promise((resolve, reject) => {
		uni.request({
			...params,
			url: baseUrl + params.url,
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success(res) {
				// console.log(123);
				const code = res.data.code;
				//token
				// console.log(res);
				if (code == 2000) {
					msg(",!");
					store.commit('logout');
					reject(res.data.msg);
					setTimeout(() => {
						uni.switchTab({
							url: '/pages/home/index'
						})
					}, 1200)
					return;
				}
				resolve(res.data)
			},
			fail(err) {
				msg("!");
				reject(err)
			}
		})
	})
}
